import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminDeviceComponent } from './admin-device.component';

const routes: Routes = [
  {
    path: 'duplicate/:deviceid',
    component: AdminDeviceComponent,
    data: { type: 'duplicate' },
    pathMatch: 'full',
  },
  {
    path: ':tenantId/duplicate/:deviceid',
    component: AdminDeviceComponent,
    data: { type: 'duplicate' },
    pathMatch: 'full',
  },
  {
    path: 'create',
    component: AdminDeviceComponent,
    data: { type: 'create' },
    pathMatch: 'full',
  },
  {
    path: ':tenantId/create',
    component: AdminDeviceComponent,
    data: { type: 'create' },
    pathMatch: 'full',
  },
  {
    path: ':deviceid',
    component: AdminDeviceComponent,
    data: { type: 'properties' },
    pathMatch: 'full',
  },
  {
    path: ':tenantId/properties/:deviceid',
    component: AdminDeviceComponent,
    data: { type: 'properties' },
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminDeviceRoutingModule {}
